.range-scale {
  margin: 0 auto;
  width: 210px;
  padding-bottom: 40px;
  text-align: center;
  text-transform: uppercase;
  color: @text-contrast-color;
  display: flex;

  @media (min-width: @tablet-width) {
    width: 592px;
    padding-bottom: 55px;
  }
}

.range-scale__description {
  text-align: center;
  text-transform: uppercase;
  color: @text-contrast-color;
  padding: 5px;
  .additional-title-style ();

  @media (min-width: @tablet-width) {
    border-bottom: 1px dotted #cdcdcd;
    padding: 2px 0;
    margin: 0 10px  0 13px;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;

    &:hover {
      border-bottom: 1px solid #cdcdcd;
    }

    &:active {
      border-bottom: none;
    }
  }
}

.range-scale__range-bar {
  position: relative;
}

.range-scale__scale {
  width: 86px;
  height: 14px;
  margin: 8px 13px;
  appearance: none;
  border: @catalog-additional-border;
  box-sizing: border-box;
  border-radius: 6px;

  @media (min-width: @tablet-width) {
    width: 426px;
    height: 6px;
    margin: 17px 13px;
    border: none;
  }
}

.range-scale__scale::-webkit-slider-runnable-track {
  height: 6px;
  margin: 4px 40px 4px 6px;
  background: @button-main-color;
  border-radius: 6px;

  @media (min-width: @tablet-width) {
    margin: 0;
    padding: 0;
    background: #dcdcdc;
  }
}

.range-scale__scale::-webkit-slider-thumb {
  appearance: none;
}

.range-scale__toggle {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    position: absolute;
    top: 0;
    left: 213px;
    width: 34px;
    height: 34px;
    background-color: @main-color;
    border-radius: 50%;
    border: @catalog-additional-border;
    filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));

    &:hover {
      filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
    }

    &:active {
      border: 1px solid #68b738;
      filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
    }

    &::before {
      content: "";
      position: absolute;
      top: 12px;
      left: 12px;
      background-color: @button-main-color;
      border-radius: 50%;
      height: 8px;
      width: 8px;
    }
  }
}
